<template>
    <v-container fluid>
        <v-slide-y-transition mode="out-in">
            <tabs :words="words.shapes" :enName="enName" :esName="esName" :enTitle="enTitle" :esTitle="esTitle"
                  :exercises="exercises"
                  :fab="fab" :textEnable="textEnable" :soundPath="getSoundPath" :img="img" :imgPath="getImgPath">
            </tabs>
        </v-slide-y-transition>
    </v-container>
</template>

<script>
    import {soundShapesPath, imgShapesPath} from '../../config/config'
    import Tabs from '../tabs/Tabs.vue'
    import {mapState} from 'vuex'

    export default {
        name: 'Shapes',
        components: {Tabs},
        computed: {
            ...mapState([
                'words'
            ]),
            getSoundPath: function () {
                return soundShapesPath;
            },
            getImgPath: function () {
                return imgShapesPath;
            }
        },
        data: function () {
            return {
                fab: false,
                textEnable: false,
                img: true,
                enName: "shapes",
                esName: "las figuras geometricas",
                enTitle: "Shapes",
                esTitle: "Shapes",
                exercises: {
                    listen: {enable: true},
                    read: {enable: true},
                    dictation: {enable: true},
                    pairs: {enable: true},
                    remember: {enable: true}
                }
            }
        }
    }
</script>
